      description: '<p>⭐️PLEASE REGISTER VIA EVENTBRITE : <a href=\'https://lew.ag/mucssfeb\' class=\'linkified\'>https://lew.ag/mucssfeb</a> ⭐️</p> <p>DISCLAIMER: you should have some basic HTML/CSS knowledge to come.</p> <p>This is a FREE workshop on CSS Components Design!</p> <p>If you already have some knowledge of HTML, CSS and have briefly played with Bootstrap (<a href=\'http://getbootstrap.com/\' class=\'linkified\'>http://getbootstrap.com/</a>), then this workshop will be great fun.</p> <p>If you\'ve been to one of our \'Build your landing page in 2 hours\' / \'Introduction to HTML &amp; CSS\' workshops, this will be the perfect follow-up.</p> <p>👇In this workshop you will be learning to code 👇</p> <p>- Navbars</p> <p>- Buttons</p> <p>- Cards</p> <p>- Dropdown-lists</p> <p>- Banners &amp; badges</p> <p>- Other UI components that you can find on most apps</p> <p>To do that, we will cover lots of cool CSS techniques and patterns by reproducing components from existing websites (Product Hunt or Medium). Positioning techniques, flexbox, transitions, filters, etc.. you\'ll learn a lot of crazy CSS stuff, we promise!</p> <p>👇How to prepare and what to bring👇</p> <p>1) Your own laptop - FULLY CHARGED</p> <p>2) Make sure you have Google Chrome installed and running on your computer: <a href=\'https://www.google.com/chrome/\' class=\'linkified\'>https://www.google.com/chrome/</a></p> <p>3) Download SublimeText Editor: <a href=\'http://www.sublimetext.com/3\' class=\'linkified\'>http://www.sublimetext.com/3</a> (<a href=\'http://www.sublimetext.com/2\' class=\'linkified\'>http://www.sublimetext.com/2</a>)</p> <p>⭐️PLEASE REGISTER VIA EVENTBRITE : <a href=\'https://lew.ag/mucssfeb\' class=\'linkified\'>https://lew.ag/mucssfeb</a> ⭐️</p> <p>***************************************</p> <p>Le Wagon is Europe\'s leading coding school for entrepreneurs and creative people. We are now present in 35 cities worldwide, with over 120 startups in operation, and have been rated the no#1 coding bootcamp worldwide on Course Report &amp; Switchup! Our full-stack web development course equips aspiring entrepreneurs, product managers, designers, and junior developers with the technical skills they need to build great products and startups!</p> <p>The next batch in Munich starts on March 30th 2020. Further details about the course and how to apply can be found here (<a href=\'https://www.lewagon.com/munich\' class=\'linkified\'>https://www.lewagon.com/munich</a>).</p> <p>👉Join our Meetup group: <a href=\'https://www.meetup.com/de-DE/Le-Wagon-Munich/\' class=\'linkified\'>https://www.meetup.com/de-DE/Le-Wagon-Munich/</a></p> <p>👉Like us on Facebook: <a href=\'http://facebook.com/lewagongermany\' class=\'linkified\'>http://facebook.com/lewagongermany</a></p> <p>👉Follow us on Twitter: <a href=\'https://twitter.com/lewagonmunich\' class=\'linkified\'>https://twitter.com/lewagonmunich</a></p> <p>👉Look at some lovely photos on Instagram: <a href=\'https://www.instagram.com/lewagonmunich/\' class=\'linkified\'>https://www.instagram.com/lewagonmunich/</a></p> <p>***************************************</p> <p>We look forward to meeting you soon!</p> <p>Team Le Wagon</p> ',
