      description: '<p>* No More Mr Nice Guy Intensiv-Gruppe *</p> <p>(Letzte Chance mit einzusteigen – danach keine neuen Teilnehmer)</p> <p>___</p> <p>In dieser 3-monatigen Wachstumsgruppe hast du einen Container für deine persönliche Entwicklung.</p> <p>Gemeinsam mit anderen zielstrebigen Männern gehst du mutige Schritte auf neue Ziele zu, erhältst Coaching zu deiner individuellen Situation, wertvollen Input und ein hochwertiges aufeinander aufbauendes Programm an jedem Abend.</p> <p>In den 3 Monaten hast Du…</p> <p>&gt; Eine feste Gruppe von 10 Männern<br/>&gt; 2 Abende / Monat je 3 Stunden intensives Coaching<br/>&gt; Wöchentliche Video-Chats zum Einchecken und Ziele setzen<br/>&gt; Eine WhatsApp-Gruppe für den Austausch</p> <p>Als Teil dieser Gruppe kannst du massive Veränderungen in deinen Lebensbereiche bewirken. Egal ob Du bei Frauen, im Dating, Beruflich oder Privat fortschritte machen willst – hier bist du richtig.</p> <p>&gt; Erhalte absolute Klarheit für deine Wünsche<br/>&gt; Setze dir perfekt definierte Ziele<br/>&gt; Entwickle den Ehrgeiz, sie zu verfolgen<br/>&gt; Nutze eine Mastermind-Gruppe, die Dich fordert<br/>&gt; Lerne effektive Tools und Strategien für den Weg<br/>&gt; Erreiche deine Ziele und feiere sie mit uns</p> <p>Diese Gruppe hilft dir…</p> <p>&gt; Commitment und Tatendrang zu entwickeln<br/>&gt; Dran zu bleiben, wenn es schwierig wird<br/>&gt; Dein volles Potential zu erkennen und zu leben<br/>&gt; Dich zu hinterfragen, optimieren, und akzeptieren</p> <p>___</p> <p>Teilnahme für die 3 Monate: 360 EUR</p> <p>Termine – Donnerstags, jeweils ab 19Uhr</p> <p>&gt;[masked]<br/>&gt;[masked]<br/>&gt;[masked]<br/>&gt;[masked]<br/>&gt;[masked]<br/>&gt;[masked]</p> <p>___</p> <p>Dieses Meetup ist keine Therapie oder Heilbehandlung.<br/>Als Teilnehmer nimmst Du davon Kenntnis, dass es sich bei diesem Seminarangebot nicht um eine Therapie im psychotherapeutischen oder klinischen Sinne handelt und diese nicht ersetzt. Dir bekannte psychische Probleme (z.B. Depression, Psychose, Trauma) sowie starke körperliche Einschränkungen (z.B. Herzkrankheiten, Epilepsie) teilst Du der Leitung vorab mit.</p> <p>Lass uns zusammen wachsen!</p> ',
