      link: 'https://www.meetup.com/GDG-cloud-munich/events/267972434/',
      description: '<p>Let\'s kick start new journey into 2020 with our great friends Stylight HQ.</p> <p>⏰ Schedule:<br/>• 18:30 - 18:50: 🍕Doors open<br/>• 18:50 - 19:00: 📢 Announcements<br/>• 🎤 Laurent Picard<br/>Building smarter solutions with no expertise in machine learning.<br/>• 🎤Zack Akil<br/>Safer cycling with an Edge TPU watching your back<br/>• 🎤Polong Lin<br/>Machine learning at scale with BigQuery, Colab and AI Platform notebooks on Google Cloud<br/>• 21:30 - 22:00: Networking</p> <p>🔘 Laurent Picard – Building smarter solutions with no expertise in machine learning.</p> <p>ML? API? AutoML? What can we build in only a few hours or days? In this session, we\'ll see how to benefit from existing ML models and how to create a custom model with AutoML techniques. We’ll also be active players of a live demo, so don\'t put your smartphone on airplane mode!</p> <p>Laurent is a developer passionate about software, hardware, science, and everything shaping the future. He works for Google Cloud where he enjoys exploring and sharing what\'s possible. In a prior life, he pioneered the ebook industry, co-created the 1st European ebook reader, and co-founded Bookeen.</p> <p>🔘 Zack Akil – Safer cycling with an Edge TPU watching your back</p> <p>Cycling in central London is an extreme sport! To give me the competitive edge I\'ve built a machine vision model that runs on an Edge TPU that keeps me informed about the dangerous vehicles that are behind me in blistering real time. I\'ll show you the end-to-end process of developing and deploying the model to the recently released Edge TPU hardware from Google.</p> <p>Zack is a developer advocate for Cloud Machine Learning at Google. When he’s not teaching machine learning at Google, he likes to teach machine learning at his hands-on data science meetup, Central London Data Science Project Nights. Although he works in the cloud, most of his hobby projects look at different ways you can embed machine learning into low-power devices like Raspberry Pis and Arduinos. He also likes to have a bit of banter with his mixed tag rugby teams.</p> <p>🔘 Polong Lin – Machine learning at scale with BigQuery, Colab and AI Platform notebooks on Google Cloud</p> <p>How can data scientists scale their machine learning pipelines with minimal data engineering overhead? Polong Lin (Developer Advocate, Google) will discuss how BigQuery ML, Colab and AI Platform can be used together to help businesses accelerate their growth with data.</p> <p>Polong is a Developer Advocate at Google, based in London, UK, with a focus on data science and machine learning. Due to his background in psychology, Polong gets particularly excited about data science projects at the intersection of data science and human behaviour.</p> <p>👀🌟 We are looking forward for next events, if you have topic suggestion, or want to talk we are open for speakers and sponsors 🤝</p> <p>See you at the meetup!</p> <p>-- Yevgen and Mario</p> ',
