      description: '<p>Diesmal zeigt Andreas Heinrich die einzelnen Schritte, wie man mit Elementor eine Webseite erstellt. Im einzelnen:</p> <p>• Bau oder Nachbau einer schönen Unterseite<br/>• Strukturierung der Inhalte<br/>• Integration von Schriften, Farben und Stilen<br/>• Definition und Funktion der einzelnen Elemente<br/>• Live Design der Seite mit Kommentierung der einzelnen Schritte</p> <p>Das Ziel für Einsteiger: Wie strukturiert und baut ein Profi eine Webseite?</p> <p>Interessant aber auch für Profis: Austausch und Diskussion!</p> <p>Hinweise:<br/>1. Wir sind zu Gast in der Agentur von Andreas Heinrich. Der Platz ist eingeschränkt. Deswegen bitte schnellstmöglich anmelden.<br/>2. Zum Netzwerken ist es sinnvoll, Visitenkarten mitzubringen und sich gegenseitig auszuhändigen. Das hilft, langfristig in Kontakt zu bleiben.</p> <p>Wir gehen anschließend in das Restaurant Trattoria Sankt Benno (Loristraße 14), wenige Schritte entfernt. Dort gibt es weiter Gelegenheit Fragen zu stellen und sich zum Thema Elementor auszutauschen. Wer mitkommen will, ist herzlich eingeladen.</p> ',
