      link: 'https://www.meetup.com/Julia-User-Group-Munich/events/267773569/',
      description: '<p>Let\'s kickoff the Julia User Group in Munich.</p> <p>My plans for this meetup series is to give an opportunity to stay up to date with most recent Julia developments, and to find support for bringing your next Julia ideas to life.<br/>Let\'s create a solid community, starting with building up a support network, and going to spread Julia more and more into the Munich Data Science world.</p> <p>Rough agenda for the first meeting:<br/>* 18:30 We will start with an informal getting-to-know each other. We have free food and drinks !!<br/>* 19:00 Introductory presentation about the Julia language and its strengths. I will give some background, how Julia is able to achieve to flow like Python and run like C, and compare Julia to other common Data Science languages like R, Python, Fortran, Scala, Matlab.<br/>* 20:00 Open round-table to gather possible future directions of Julia User Group Munich. I will present some projects and ideas of mine and have suggestions about what to tackle next. Everyone is warmly invited to shape Julia User Group Munich!</p> <p>Even if you are just curious about the language and don\'t plan to work with it anytime soon, you are welcome to drop by to check it out!</p> <p>----------------------</p> <p>Thanks to Data Reply we will have an awesome location plus food and drinks for this first meetup!</p> ',
