      link: 'https://www.meetup.com/Product-School-Munich/events/268115821/',
      description: '<p>***RSVP on EventBrite:  ***</p> <p>Product Management in the technology business is one of the most exciting career paths. It is creative yet challenging. Discover and discuss the path of entry and succeeding in Product Management in Technology Business.</p> <p>Understand and discuss with Vibhuti Ranjan Singh, what requires to succeed in Product Management, types of Product Management roles and what are possible career paths.</p> <p>Organizer:<br/>Gergana Drockur - Product School Community Chapter Lead Munich<br/>LinkedIn ()<br/>Gergana is a Product Advisor with 12+ years of experience in the digital space. Her passion is about Product Innovation &amp; New Businesses. Gergana has worked with and consulted a large number of top companies on Product Management, Product Strategy, New Business Development, Product Innovation as well as Venture Building with Remote Teams.</p> <p>Special thanks to Thomas Bender and AWS for hosting the event in its great location in the center of Munich.</p> <p>Meet the Speaker: Vibhuti Ranjan Singh</p> <p>Vibhuti Ranjan Singh - Global Product Director Ernst &amp; Young Creative digital platform products leader. Built successful businesses, products &amp; teams in the world\'s leading technology companies. Worked with the brightest minds in eBay, Groupon, Nokia &amp; GE. Currently working as Global Product Director @ EY, creating new AI-based products and business models. Vibhuti loves pairing strategic business thinking with technological possibilities. Experience spans across multiple functions – Strategy, Product Management, Product Marketing, and Engineering in technology businesses. Educated with the degree in MBA, Bachelor in Software Engineering and continuously curious.</p> <p>View the Speaker\'s LinkedIn ()<br/>--<br/>Please note:<br/>- No refunds unless we have fully canceled the event. Date changes are not included.<br/>- For additional info or questions, send an email to [masked]</p> <p>Product School () is the global leader in product management training, with over 10,000 alumni, 20 campuses worldwide and a live online campus. Product School’s certificates (PMC™,FPMC™,PLC™) are the most important industry-recognized certificates for product managers; all of our instructors are senior-level product managers working at top technology companies such as Google, Facebook, Airbnb, PayPal, LinkedIn, and Netflix.</p> <p>.</p> <p>Get Your Tickets ()</p> <p>*Entrance not guaranteed without valid ticket from Eventbrite</p> ',
