      link: 'https://www.meetup.com/Azure-DEV-Munich/events/267906496/',
      description: '<p>We are in a new decade, the 20ies!<br/>And we want to see and greet all of you! And we have some tremendous News to share!<br/>So - let\'s see what\'s in in 2020!<br/>What\'s up in Azure and what\'s next on the Meetup Roadmap?<br/>Join us for a evening about Hot News in Azure shared by MVP Martin Brandl. But before going into the hot news, we have to share the Roadmap of Azure Dev Meetup Munich with you.<br/>Expect cool Events and Topic series!</p> <p>Agenda:<br/>• 18:00 - 18:30: Doors open &amp; networking<br/>• 18:30 - 19:00: Roadmap Azure Dev Meetup Munich<br/>• 19.00 - 20.00 : What\'s up in Azure 2020 - News and updates about Azure<br/>• 20:00 - 20:15: Azure Freestyle - your stage!</p> <p>Speaker:<br/>Martin Brandl | white ducks | Azure MVP<br/>For Martin, the Azure Cloud platform has been a toolbox for over 8 years. He knows where he can use which tool effectively and thus always finds a suitable solution. He is convinced that the modern developer is a composer whose key competence is to orchestrate solutions and to use them meaningfully. The Azure MVP status underlines his know-how around the Azure Cloud. Martin focuses on the architecture of modern Cloud and SaaS business applications and their automated deployment with tools such as Azure DevOps, PowerShell and Azure Resource Manager (ARM).</p> ',
