      link: 'https://www.meetup.com/pass-bayern/events/267182421/',
      description: '<p>In this session we will explore the concept of Chaos Engineering, its core concepts, and how it has been implemented at various companies.</p> <p>Then we\'ll look at how it can be implemented with regard to SQL Server. SQL has various different high availability solutions but can you be sure that they\'ll react as expected to a real world issue? Has your HA architecture only ever been tested in a planned maintenance window?</p> <p>We\'ll also have some fun by looking at KubeInvaders, a chaos engineering tool for Kubernetes...using Space Invaders!</p> <p>This session is for SQL Server DBAs and Developers who want to know how Chaos Engineering works and want to learn its main principles.</p> <p>Andrew Pruski</p> <p>SQL Server DBA originally from Wales but now exploring Ireland</p> <p>I\'m a SQL Server DBA and Microsoft Data Platform MVP, you can find me on twitter @dbafromthecold and blogging at dbafromthecold.com</p> ',
