      link: 'https://www.meetup.com/Swift-Meetup-Munich/events/268058147/',
      description: '<p>Hola Swifteristas!</p> <p>Time flies..! Happy new year and hopefully you had a wonderful time during the years.</p> <p>As our calendars already turned to 2020 we want to tune into the year with a fine gathering learning and chatting about our favourite topic Swift.</p> <p>This time we are hosted at a special location ☺️🥳!!!</p> <p>The final address as well as the kick-ass agenda will be shared in our Slack channel a couple of days before - so stay tuned there!<br/>But just for planing reasons - the event will take place a couple of walking minutes away from Karlsplatz/Stachus.</p> <p>__Agenda__</p> <p>let tbd =<br/>let you : String? = your name goes here!</p> <p>18:30 - 🚪 Doors open<br/>We will open the doors for delicious pizza &amp; beer to have something to hold on to while talking and chatting. Get enthusiastic for the first talk!</p> <p>19:15 - 🕖 Session I<br/>let title = tbd<br/>let abstract = tbd</p> <p>19:40 - 🕢 Session II<br/>let title = tbd<br/>let abstract = tbd</p> <p>20:10 - 🕗 Session III<br/>let title = tbd<br/>let abstract = tbd</p> <p>⚠️ Call to action ⚠️<br/>If anybody of you has a topic they is enthusiastic and passionate about, just drop us a note. We are always looking for new speakers.<br/>And to all newbie speakers: Just give it a try, accept the challenge - we don\'t bite and you\'ll be super proud of yourself afterwards!</p> ',
